;AACA;;IAEI,wEAAwE;IACxE,kDAAkD;AACtD;AACA;;IAEI,sEAAsE;IACtE,gDAAgD;AACpD;;AAEA;IACI,mBAAmB;IACnB;AACJ;;;AAGA,oFAAoF;;;AAGpF;IACI,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,kBAAkB;AACtB;;AAEA;IACI,UAAU;IACV,eAAe;AACnB;;AAEA,sFAAsF;;AAEtF;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,sBAAsB;AAC1B","sourcesContent":["/*\nGENERAL RULES:\n- every rule should be unambiguously scoped by bamboolib so that there is no interference with any other components\n    - GOOD: .bamboolib-ui.jupyter-widgets\n    - BAD: .selectize-input\n        - bad because this class might be used by other people as well\n        - possible fix:\n            - .bamboolib-ui.selectize-input\n- general fix:\n    - we add our .bamboolib-ui scoping to any output that we create!\n*/\n\n\n:root {\n    --bamboolib-button-border-radius: 5px;\n    --bamboolib-primary-color-1: #D9EAD3;\n    --bamboolib-primary-color-2: #6AA84E;\n    --bamboolib-primary-color-3: #38761c;\n    --bamboolib-secondary-color-1: #EFEFEF;\n    --bamboolib-secondary-hover-color-1: #f0f0f0;\n    --bamboolib-secondary-color-2: #535353;\n    --bamboolib-secondary-hover-color-2: #DDD;\n    --bamboolib-xxs-size: 85px;\n    --bamboolib-xs-size: 105px;\n    --bamboolib-sm-size: 155px;\n    --bamboolib-md-size: 205px;\n    --bamboolib-lg-size: 240px;\n    --bamboolib-xl-size: 360px;\n    --bamboolib-info-notification-background-color: #cce5ff;\n    --bamboolib-info-notification-color: #004085;\n    --bamboolib-info-notification-link-color: #002752;\n    --bamboolib-success-notification-background-color: #d4edda;\n    --bamboolib-success-notification-color: #155724;\n    --bamboolib-success-notification-link-color: #0b2e13;\n    --bamboolib-warning-notification-background-color: #fff3cd;\n    --bamboolib-warning-notification-color: #856404;\n    --bamboolib-warning-notification-link-color: #533f03;\n    --bamboolib-error-notification-background-color: #f8d7da;\n    --bamboolib-error-notification-color: #721c24;\n    --bamboolib-error-notification-link-color: #491217;\n}\n\n.bamboolib-width-xxs {\n    width: var(--bamboolib-xxs-size) !important;\n    max-width: var(--bamboolib-xxs-size) !important;\n}\n\n.bamboolib-width-xs {\n    width: var(--bamboolib-xs-size) !important;\n    max-width: var(--bamboolib-xs-size) !important;\n}\n\n.bamboolib-width-sm {\n    width: var(--bamboolib-sm-size) !important;\n    max-width: var(--bamboolib-sm-size) !important;\n}\n\n.bamboolib-width-md {\n    width: var(--bamboolib-md-size) !important;\n    max-width: var(--bamboolib-md-size) !important;\n}\n\n.bamboolib-width-lg {\n    width: var(--bamboolib-lg-size) !important;\n    max-width: var(--bamboolib-lg-size) !important;\n}\n.bamboolib-width-xl {\n    width: var(--bamboolib-xl-size) !important;\n    max-width: var(--bamboolib-xl-size) !important;\n}\n\n.bamboolib-pivot-html .output div.output_subarea, /* jupyter notebook */\n.bamboolib-pivot-html div.jp-OutputArea /* jupyter lab */ {\n    overflow-x: auto;\n    overflow-y: auto;\n    max-height: 80vh;\n}\n\n.bamboolib-slider .widget-readout {\n    border: 1px solid #e0e0e0;\n}\n\n/* ---------------------------------------------- */\n/* Jupyter Notebook */\n\n/* ipywidgets have 2px margin by default. The following to settings gives same\nspacing to selectize so that selectize dropdowns and ipywidgets text work well\ntogether */\n.bamboolib-ui .selectize-input {\n    margin: 0px;\n}\n.bamboolib-ui .selectize-control {\n    margin: var(--jp-widgets-margin);\n}\n\n/* make ipywidgets-text look like selectize */\n.bamboolib-input.widget-text input[type=\"text\"],\n.bamboolib-input.widget-text input[type=\"number\"],\n.bamboolib-input.widget-textarea textarea,\n.bamboolib-input.widget-textarea {\n    font-size: 13px !important;\n    border: 1px solid #ccc;\n    min-height: 34px;\n    padding: 6px 12px;\n    display: inline-block;\n    width: 100%;\n    overflow: hidden;\n    position: relative;\n    z-index: 1;\n    -webkit-box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    box-sizing: border-box;\n    -webkit-box-shadow: none;\n    box-shadow: none;\n    -webkit-border-radius: 4px;\n    -moz-border-radius: 4px;\n    border-radius: 4px;\n}\n/* Have some spacing from parent div that we need to revert so that we are\naligned with selectize */\n/* .bamboolib-input.widget-text input[type=\"text\"] {\n    margin-top: 0px;\n    margin-left: 0px;\n} */\n\n.bamboolib-input.widget-text input[type=\"text\"]:focus,\n.bamboolib-input.widget-text input[type=\"number\"]:focus,\n.bamboolib-input.widget-textarea textarea:focus,\n.bamboolib-input.widget-textarea:focus {\n    border-color: #66afe9;\n    outline: 0;\n    -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6);\n    box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(102, 175, 233, 0.6);\n}\n\n/* remove the arrows in the text input that allows to increment/decrement the number */\n.bamboolib-float-text input[type=number]::-webkit-inner-spin-button,\n.bamboolib-float-text input[type=number]::-webkit-outer-spin-button {\n    -webkit-appearance:none;\n    margin:0;\n}\n.bamboolib-float-text input[type=number] { /* Firefox */\n    -moz-appearance:textfield;\n}\n\n.bamboolib-input-md {\n    /* I use !important deliberately here. A user that uses this class, wants her widget to be a\n    certain size, so no need to increase specificity here */\n    width: var(--bamboolib-md-size) !important;\n}\n.bamboolib-input-md input {\n    min-height: 24px !important;\n    padding: 4px 10px !important;\n}\n\n.bamboolib-hide-label label {\n    visibility:hidden;\n}\n\n/* ---------------------------------------------- */\n/* Jupyter Lab compatibility */\n\n/* try to override with specificity and not with !important */\n/* great specificity guide: https://dev.to/emmawedekind/css-specificity-1kca */\n\n\n/* in the qgrid extension someone added overflow auto with !important for JupyterLab thus we used to need important and massive specificity in some places in order to overwrite the other important*/\n\n/* still needed because edaviz does not have the bamboolib entry class on the highest component */\n.jupyter-widgets.widget-slider {\n    overflow: hidden !important;\n}\n\n.jupyter-widgets.widget-checkbox {\n    overflow: visible !important;\n}\n\n/* the high specificity is needed in the following */\n.widget-hslider.widget-inline-hbox .widget-readout {\n    height: calc(var(--jp-widgets-inline-height) - 2px);\n}\n\n.widget-html-content .jp-RenderedHTMLCommon table {\n    margin-left: 0;\n}\n\n\n/* additional styles */\n.widget-html-content h4, h5 {\n    margin: 0;\n}\n\n\n/* ---------------------------------------------- */\n\n\n/* fix output widgets area overflow bug in Jupyter */\n/* output_scroll - the name of this class is set by jupyter and not by us - thus it has a _ in the name and not a - */\n.output_scroll, /* Jupyter notebook */\n.jp-Cell-outputArea /* Jupyter lab 1.2.7 */ {\n    height: 100% !important;\n    max-height: none !important;\n    box-shadow: inset 0 0 0 !important;\n}\n\n\n.bamboolib-output-wrap-text pre {\n    /* will show all error messages with lines breaks */\n    /* for example for the following code: df[\"new_column\"] = df[\"Timestep\"].str.replace('Day ',''\").astype(int).astype(np.timedelta64(1,'D')) */\n    white-space: pre-line;\n    word-break: break-all;\n    word-wrap: break-word;\n}\n\n/* .bamboolib-window .js-plotly-plot .plotly .modebar {\n    display: none !important;\n} */\n\n\n/* makes sure that plotly figure widgets within bamboolib e.g. PlotCreator will show a horizontal scrollbar if needed */\n.bamboolib-ui .js-plotly-plot {\n    overflow-x: visible;\n}\n\n\n/* Load spinner */\n\n.bamboolib-load-spinner {\n    display: inline-block;\n    border: 3px solid var(--bamboolib-primary-color-1);\n    border-top: 3px solid var(--bamboolib-primary-color-3);\n    border-radius: 50%;\n    width: 20px;\n    height: 20px;\n    animation: bamboolib-spin 2s linear infinite;\n    box-sizing: border-box;\n}\n\n@keyframes bamboolib-spin {\n    0% { transform: rotate(0deg); }\n    100% { transform: rotate(360deg); }\n}\n\n.bamboolib-wrangler-shape-label {\n    height: auto !important;  /* fixes bug that label shows a vertical scrollbar */\n}\n\n.bamboolib-element-next-to-selectize {\n    padding-top: 3px;\n}\n\n.bamboolib-base-button.bamboolib-element-next-to-selectize {\n    /* Attention: padding-top should be the same as in the base button */\n    /* However, this is already handled implicitly due to CSS specificity */\n    /* padding-top: 0px; */\n    margin-top: 5px;\n}\n\n/* -------------------------------- Buttons -------------------------------- */\n\n/* We added our own base-button class in order to be independent of the ipywidgets Button styling of .jupyter-button - both on Jupyter notebook and lab */\n.bamboolib-base-button {\n    font-size: 13px;\n    border-radius: var(--bamboolib-button-border-radius) !important;\n    width: -moz-fit-content;\n    width: -webkit-fit-content;\n    width: -ms-fit-content;\n    padding-left: 20px;\n    padding-right: 20px;\n    padding-top: 0px;\n    padding-bottom: 0px;\n    display: inline-block;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    text-align: center;\n    cursor: pointer;\n    box-shadow: none;\n    border: none;\n    user-select: none;\n    height: 28px;\n    margin: 1px\n}\n.bamboolib-base-button:active {\n    outline:none;\n}\n.bamboolib-base-button:focus {\n    outline:none;\n}\n.bamboolib-base-button:disabled {\n    opacity: 0.6; /* copied from ipywidgets .jupyter-button originally: var(--jp-widgets-disabled-opacity); */\n}\n\n/* styles for icon buttons with or without description */\n.bamboolib-base-button i.fa {\n    margin-right: 4px; /* copied from ipywidgets .jupyter-button originally: var(--jp-widgets-inline-margin); */\n    pointer-events: none;\n}\n.bamboolib-base-button i.fa.center {\n    /* the center style is added by ipywidgets when the button does not have a description */\n    margin-right: 0; /* copied from ipywidgets .jupyter-button */\n}\n.bamboolib-icon-button {\n    padding-left: 10px;\n    padding-right: 10px;\n    overflow: visible;\n}\n\n.bamboolib-button-primary,\n.bamboolib-button-primary:hover:disabled {\n    background-color: var(--bamboolib-primary-color-2) !important;\n    color: #FFFFFF !important;\n}\n.bamboolib-button-primary:hover {\n    background-color: var(--bamboolib-primary-color-3) !important;\n}\n.bamboolib-button-primary:focus {\n    background-color: var(--bamboolib-primary-color-3) !important;\n    border-color: var(--bamboolib-primary-color-2) !important;\n}\n\n.bamboolib-button-secondary,\n.bamboolib-button-secondary:hover:disabled {\n    background-color: var(--bamboolib-secondary-color-1) !important;\n    color: var(--bamboolib-secondary-color-2) !important;\n}\n.bamboolib-button-secondary:hover,\n.bamboolib-button-secondary:focus {\n    background-color: var(--bamboolib-secondary-hover-color-2) !important;\n}\n\n.bamboolib-button-secondary-outline {\n    border-radius: var(--bamboolib-button-border-radius) !important;\n    background-color: white !important;\n    color: var(--bamboolib-secondary-color-2) !important;\n    border: 1px solid var(--bamboolib-secondary-color-2) !important\n}\n.bamboolib-button-secondary-outline:hover {\n    background-color: var(--bamboolib-secondary-color-1) !important;\n}\n\n.bamboolib-button-function-hint {\n    border-radius: var(--bamboolib-button-border-radius);\n    background-color: white !important;\n    color: darkgrey !important;\n    width: -moz-fit-content !important;\n    width: -webkit-fit-content !important;\n    width: -ms-fit-content !important;\n}\n\n/* SVG button */\n.bamboolib-svg-button {\n    width: 34px !important;\n    height: 34px !important;\n    padding: 2px 2px 2px 2px;\n    background-color: #fff;\n    border: 0px;\n    border-radius: 50%;\n    margin: 2px;\n}\n.bamboolib-svg-button:hover {\n    background-color: var(--bamboolib-secondary-hover-color-1);\n    box-shadow: none !important;\n}\n.bamboolib-svg-button:focus,\n.bamboolib-svg-button:active {\n    outline: none;\n    background-color: var(--bamboolib-secondary-hover-color-2);\n}\n\n/* -------------------------------- Windows and Modals -------------------------------- */\n\n.bamboolib-window {\n    display: none !important;\n\n    overflow: auto; /* Enable scroll if needed */\n    background-color: white;\n\n    border: #d4d9dd 1px solid;\n    border-radius: 2px;\n    padding: 10px;\n}\n\n.bamboolib-window-header {\n    align-items: center;\n    justify-content: space-between;\n    padding: 0rem 0rem 1.5rem;\n    width: 100%;\n}\n\n.bamboolib-min-height-for-error-modal {\n    min-height: 30em;  /* 400px but resolution independent */\n}\n\n.bamboolib-window-without-border {\n    display: none !important;\n\n    overflow: auto; /* Enable scroll if needed */\n    background-color: white;\n}\n\n.bamboolib-loader-modal {\n    /* class needs to be defined before bamboolib-active-window */\n    display: none !important;\n    position: absolute;\n    z-index: 9999;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    overflow: auto; /* Enable scroll if needed */\n    background-color: white;\n    opacity: 0.8;\n\n    /* Require display: flex */\n    align-items: center; /* center at top */\n    justify-content: center; /* center in flow-direction */\n}\n\n\n.bamboolib-active-window {\n    display: flex !important;\n}\n\n.bamboolib-full-modal {\n    position: absolute;\n    z-index: 9999;\n    right: 0;\n    top: 0;\n    min-width: 100%;\n    min-height: 100%;\n}\n\n.bamboolib-side-window {\n    min-width: 440px;\n}\n\n@keyframes show_bamboo_modal_from_bottom {\n    from {top:50vh; opacity:0; height: 0;}\n    to {top:0; opacity:1; height: 100%}\n}\n\n.bamboolib-active-modal {\n    anim